function dropLists () {
  const newsTrigger = document.querySelector('.header__drop-down-menu-trigger')
  const newsMenu1 = document.querySelector('.header__drop-down-list_1_news')
  const newsMensTrigger = document.querySelectorAll('.header__drop-down-list_men-trigger')
  const newsWomensTrigger = document.querySelectorAll('.header__drop-down-list_women-trigger')
  const newsKidsTrigger = document.querySelectorAll('.header__drop-down-list_kids-trigger')
  const newsAccTrigger = document.querySelectorAll('.header__drop-down-list_accessories-trigger')

  const orderTrigger = document.querySelectorAll('.header__drop-list-order-trigger')

  const brandsTrigger = document.querySelectorAll('.header__drop-list-brands-trigger')

  brandsTrigger.forEach(item => {
    item.addEventListener('mouseenter', () => {
      item.querySelector('.header__drop-down-list_made-in-brands').classList.remove('disabled')
    })
  })
  brandsTrigger.forEach(item => {
    item.addEventListener('mouseleave', () => {
      item.querySelector('.header__drop-down-list_made-in-brands').classList.add('disabled')
    })
  })

  orderTrigger.forEach(item => {
    item.addEventListener('mouseenter', () => {
      item.querySelector('.header__drop-down-list_1_order').classList.remove('disabled')
    })
  })
  orderTrigger.forEach(item => {
    item.addEventListener('mouseleave', () => {
      item.querySelector('.header__drop-down-list_1_order').classList.add('disabled')
    })
  })

  newsTrigger.addEventListener('mouseenter', () => {
    newsMenu1.classList.remove('disabled')
  })
  newsTrigger.addEventListener('mouseleave', () => {
    newsMenu1.classList.add('disabled')
  })

  newsMensTrigger.forEach(item => {
    item.addEventListener('mouseenter', () => {
      item.querySelector('.header__drop-down-list_mens').classList.remove('disabled')
      if (item.querySelector('svg')) {
        item.querySelector('svg').style = 'transform : rotate(180deg)'
      }
    })
  })

  newsMensTrigger.forEach(item => {
    item.addEventListener('mouseleave', () => {
      item.querySelector('.header__drop-down-list_mens').classList.add('disabled')
      if (item.querySelector('svg')) {
        item.querySelector('svg').style = 'transform : rotate(0deg)'
      }
    })
  })

  newsWomensTrigger.forEach(item => {
    item.addEventListener('mouseenter', () => {
      item.querySelector('.header__drop-down-list_women').classList.remove('disabled')
      if (item.querySelector('svg')) {
        item.querySelector('svg').style = 'transform : rotate(180deg)'
      }
    })
  })

  newsWomensTrigger.forEach(item => {
    item.addEventListener('mouseleave', () => {
      item.querySelector('.header__drop-down-list_women').classList.add('disabled')
      if (item.querySelector('svg')) {
        item.querySelector('svg').style = 'transform : rotate(0deg)'
      }
    })
  })
  newsKidsTrigger.forEach(item => {
    item.addEventListener('mouseenter', () => {
      item.querySelector('.header__drop-down-list_kids').classList.remove('disabled')
      if (item.querySelector('svg')) {
        item.querySelector('svg').style = 'transform : rotate(180deg)'
      }
    })
  })
  newsKidsTrigger.forEach(item => {
    item.addEventListener('mouseleave', () => {
      item.querySelector('.header__drop-down-list_kids').classList.add('disabled')
      if (item.querySelector('svg')) {
        item.querySelector('svg').style = 'transform : rotate(0deg)'
      }
    })
  })

  newsAccTrigger.forEach(item => {
    item.addEventListener('mouseenter', () => {
      item.querySelector('.header__drop-down-list_accessories').classList.remove('disabled')
      if (item.querySelector('svg')) {
        item.querySelector('svg').style = 'transform : rotate(180deg)'
      }
    })
  })
  newsAccTrigger.forEach(item => {
    item.addEventListener('mouseleave', () => {
      item.querySelector('.header__drop-down-list_accessories').classList.add('disabled')
      if (item.querySelector('svg')) {
        item.querySelector('svg').style = 'transform : rotate(0deg)'
      }
    })
  })
}

export default dropLists
